import uniRequest from 'uni-request';
export default {
	data() {
		return {
			payType: 5,
			price:null,
			disPrice:null,
			code:true,
			orderInfo: {
				address: null,
				pay_method: null, // 1
				// 央采字段开始
				name: '',
				invoice: null, //发票id
				invoice_type: '0', //发票类型  //"0"
				invoice_title: "", //发票抬头
				invoice_org_code: '', //纳税人识别号
				invoice_phone: '', // 注册电话
				invoice_back_code: '', //开户银行账号
				invoice_bank: '', // 注册银行
			}
		};
	},
	
	onLoad(options) {
		if(uni.getStorageSync('userInfo').code){
			this.code = true
		}else{
			this.code = false
		}
		this.orderInfo = JSON.parse(options.info)
		this.price = options.price
		this.disPrice = options.disPrice
		this.orderInfo.count = options.count
		this.orderInfo.goods_id = options.goods_id
	},

	methods: {
		//选择支付方式
		changePayType(type) {
			console.log(type)
			this.payType = type;
		},
		//确认支付
		confirm: async function() {
			
			this.orderInfo.pay_method = this.payType
				
			console.log(this.orderInfo.goods_id)
			if(this.orderInfo.goods_id === undefined || this.orderInfo.goods_id === ''){   // 购物车购买
				const res = await uniRequest({
					url: '/orders/',
					method: 'post',
					data:this.orderInfo,
					headers: {
						Authorization: 'JWT ' + uni.getStorageSync('userInfo').token
					},
				}).then(res => {
					console.log(res)
					if(res.status === 201){
						this.$api.msg('订单提交成功')
						
						uni.redirectTo({
							url: '/pages/money/paySuccess?payType='+JSON.stringify(this.orderInfo.pay_method)+'&orderId='+ JSON.stringify(res.data.order_id)
						})
					}else{
						this.$api.msg(res.data.message)
					}
					
				}).catch(error => {
					console.log(error.data)
					
				})
			}else{
				const res = await uniRequest({  // 直接购买
					url: '/orders/directly/create/',
					method: 'post',
					data:this.orderInfo,
					headers: {
						Authorization: 'JWT ' + uni.getStorageSync('userInfo').token
					},
				}).then(res => {
					console.log(res)
					if(res.status === 201){
						this.$api.msg('订单提交成功')
						uni.redirectTo({
							url: '/pages/money/paySuccess?payType='+JSON.stringify(this.orderInfo.pay_method)+'&orderId='+ JSON.stringify(res.data.order_id)
						})
					}else{
						this.$api.msg(res.data.message)
					}
					
				}).catch(error => {
					console.log(error.data)
					
				})
			}
			
		},
	}
}
